<template>
  <v-app>
    <v-main>
      <HelloWorld v-if="test==1"/>
      <HelloWorld2 v-else-if="test==2"/>
    </v-main>
  </v-app>
</template>

<script>
import HelloWorld from './components/HelloWorld';
import HelloWorld2 from './components/HelloWorld2';

export default {
  name: 'App',

  components: {
    HelloWorld,
    HelloWorld2,
  },

  data: () => ({
    test:0,
  }),
  mounted(){
    let params = new URLSearchParams(document.location.search);
    let v = params.get("v"); 
    this.test = +v
  }
};
</script>
